body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-Basic
{
  display: flex;
  min-height: 100vh;
  text-align: center;
  justify-content: center;
  color: white;
  align-items: center;
  flex-direction: column;
  background-color: #131A28;
}

.para
{
  font-size: 40px;
  font-weight: bold;
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: white;
}

.row
{
  display: flex;
  flex-direction: row;
}

.column
{
  display: flex;
  flex-direction: column;
}

.jc-center
{
  justify-content: center;
}

.square
{
  min-width: 100px;
  min-height: 100px;
  font-size: 60px;
  text-align: center;
  color: #1CDBF5;
  font-weight: bolder;
  padding: 30px;
}

.b-bottom-right{
  border-right: 15px solid #1CDBF5;
  border-bottom: 15px solid #1CDBF5;
}

.b-bottom{
  border-bottom: 15px solid #1CDBF5;
}

.b-right 
{
  border-right: 15px solid #1CDBF5;
}

.clear
{
  margin-top: 2rem;
}

.clear
{
  font-size: 20px;
  height: 40px;
  border: 3px solid rgb(29, 228, 39);
  background-color: rgb(26, 235, 78);
}

.clear:hover
{
  background-color: rgb(216, 18, 18);
  border-color: red;
}